@use 'foundation'as *;

.main-card {
  position: relative;
  padding-bottom: prem(56);
}

.main-card__list {
  display: flex;
  gap: prem(16);
  padding-block: prem(18);
}

.main-card__item {
  display: flex;
  flex-direction: column;
  gap: prem(16);
  border: 1px solid $color-theme-right;
  background-color: $color-white;
  position: relative;
  padding: prem(16);
  height: 100%;
  box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.15000000596046448);
  ;
  border-radius: 20px;
  border: 1px solid $color-theme;
  position: relative;
  @include mq("md") {
    padding: prem(26) prem(26) prem(52) prem(26);
    gap: prem(20);
    min-height: prem(640);
    width: calc(100% / 3) !important;
  }
}

.main-card__item::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20px;
  border-radius: 0 0 20px 20px;
  background-color: $color-theme;
}

.main-card__content {
  display: flex;
  flex-direction: column;
  gap: prem(14);
  height: 100%;
  flex: 1;
}

.main-card__hero {
  width: 100%;
}

.main-card__hero img {
  width: 100%;
  height: auto;
  aspect-ratio: 269 / 167;
  object-fit: cover;
}

.main-card__thumbs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: prem(8);
}

.main-card__thumb img {
  width: 100%;
  height: 100%;
  aspect-ratio: 180 / 120;
  object-fit: cover;
}

.main-card__info {
  display: grid;
  row-gap: prem(10);
  align-content: start;
  margin-top: prem(0);

}

.main-card__name {
  font-size: prem(18);
  letter-spacing: 0.08em;
  color: $color-black;
  line-height: 1;
}

.main-card__name-mark {
  font-size: prem(14);
  line-height: 1;
  color: $color-theme;
}

.main-card__address {
  font-size: prem(14);
  letter-spacing: 0.08em;
  color: $color-theme;
  padding-top: prem(8);
  border-top: 1px solid $color-theme-right;

  @include mq("md") {
    font-size: prem(16);
  }

}

.main-card__tags {
  display: flex;
  flex-wrap: wrap;
  gap: prem(5);

  @include mq("md") {
    gap: prem(14);
  }
}

.main-card__tag {
  background-color: $color-theme;
  color: $color-white;
  font-size: prem(11);
  letter-spacing: 0.05em;
  padding: prem(8) prem(12);
  line-height: 1.4;

  @include mq("md") {
    font-size: prem(15);
  }
}

.main-card__footer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: prem(8);
  margin-top: auto;
}

.main-card__button-wrapper {
  text-align: center;
  margin-top: prem(16);

}

.main-card__label {
  position: absolute;
  top: 0;
  right: 0;
  writing-mode: vertical-rl;
  background: $main-gradient;
  color: $color-white;
  font-family: $font-family-heisei;
  font-size: prem(14);
  letter-spacing: 0.3em;
  padding: prem(12) prem(6);
  display: flex;
  align-items: center;
  gap: prem(6);
  justify-content: center;
  border-radius: 0 0 0 40px;
  font-weight: 300;

  @include mq("md") {
    font-size: prem(17);
  }
}

.main-card__label-mark {
  font-size: prem(12);
  line-height: 1;
}

.main-card__label-text {
  display: block;
  writing-mode: vertical-rl;
}

.main-card__label-long-vowel {
  font-family: $font-family-base;
}

#main-card-slider .splide__track {
  padding-block: prem(12);
}

#main-card-slider .splide__list {
  gap: prem(16);
}

#main-card-slider .splide__slide {
  height: auto;
  display: flex;
}

#main-card-slider .splide__arrow {
  display: none;
}

.main-card__pagination {
  position: absolute;
  inset-inline: prem(12);
  bottom: prem(0);
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: prem(12);
}

.main-card__counter {
  display: none;

  @include mq("md") {
    display: block;
    font-size: prem(27);
    font-weight: $bold;
    letter-spacing: 0.08em;
    color: $color-theme;
  }
}

.main-card__nav {
  display: none;

  @include mq("md") {
    display: flex;
    align-items: center;
    justify-content: end;
    gap: prem(14);
    margin-top: prem(16);
    font-weight: $bold;

  }
}

.main-card__nav-btn {
  width: prem(32);
  height: prem(32);
  border: none;
  background: none;
  color: $color-theme;
  font-size: prem(16);
  line-height: 1;
  padding: 0;
  cursor: pointer;
}

.main-card__nav-btn:focus-visible {
  outline: 1px solid $color-theme;
  outline-offset: prem(2);
}

.main-card__nav-prev::before {
  font-size: prem(20);

  content: "＜";
}

.main-card__nav-pause::before {
  font-size: prem(20);
  content: "| |";
}

.main-card__nav-next::before {
  font-size: prem(20);
  content: "＞";
}

.main-card__progress {
  position: absolute;
  inset-inline: prem(12);
  bottom: prem(10);
  height: prem(4);
  background-color: $color-theme-right;
  overflow: hidden;
}

.main-card__progress-bar {
  height: 100%;
  width: 0%;
  background-color: $color-theme;
  transition: width 0.2s linear;
}

@include mq("md") {
  .main-card__list {
    gap: prem(28);
    padding-block: prem(28);
  }



  .main-card__content {
    gap: prem(20);
  }

  .main-card__hero img {
    aspect-ratio: 570 / 368;
  }

  .main-card__thumbs {
    gap: prem(12);
  }

  .main-card__name {
    font-size: prem(24);
  }



  .main-card__label {
    font-size: prem(16);
    padding: prem(16) prem(10);
  }

  .main-card__label-mark {
    font-size: prem(14);
  }

  .main-card__pagination {
    inset-inline: prem(20);
    bottom: prem(0);
    display: flex;
  }

  .main-card__nav-btn {
    width: prem(36);
    height: prem(36);
    font-size: prem(18);
  }
}
